var wx = require("weixin-js-sdk");
import {
    Flexbox,
    FlexboxItem,
    Group,
    Cell,
    Calendar,
    XButton,
    InlineXNumber,
    Datetime,
    XNumber,
    Confirm,
    Scroller,
    XDialog
} from "vux"
import {
    global
} from "../assets/javascript/global.js"
import {
    request
} from "../assets/api/api.js"

export default {
    components: {
        Flexbox,
        Group,
        Scroller,
        Cell,
        XButton,
        Calendar,
        Datetime,
        Confirm,
        InlineXNumber,
        XNumber,
        FlexboxItem,
        XDialog
    },
    data() {
        return {
            count: 1,
            showDetail: false,
            billData: {
                "id": '',
                "car": {
                    "id": '',
                    "companyId": '',
                    "license": "",
                    "numeration": '',
                    "brand": "",
                    "type": "",
                    "coach": {
                        "userId": '',
                        "userName": "",
                        "telephone": "",
                        "companyId": null,
                        "available": true,
                        "gmtCreate": null,
                        "gender": null,
                        "identityCard": null,
                        "star": true,
                        "photo": null
                    },
                    "gmtCreate": ''
                },
                "schedules": [
                    {
                        "companyId": '',
                        "id": '',
                        "carId": '',
                        "tempCoach": '',
                        "course": "",
                        "date": '',
                        "totalNumber": '',
                        "waitNumber": '',
                        "status": "",
                        "gmtCreate": ''
                    }
                ]
            },
            chargeSetting: {
                "id": '',
                "companyId": '',
                "type": '',
                "course": '',
                "way": '',
                "unit": '',
                "fee": '',
                "discounts": '',
                "starExpense": '',
                "gmtCreate": ''
            },
            billCharge: {
                "price": '',
                "quantity": '',
                "servicePrice": '',
                "discounts": '',
                "starExpense": '',
                "totalFee": ''
            },
            type: this.$route.query.carType,
            course: this.$route.query.course,
            userInfo: JSON.parse(sessionStorage.getItem('userInfo')),
            repeatBuy: false,
            isClickBuy: false,
            status: {
                // pullupStatus: 'default'
            },
            height: ''
        }
    },
    methods: {
        // 购买数量变更，即时请求价格总数
        changeCount() {
            // console.log(this.count);
            let args = {
                "studentId": this.userInfo.student.userId,
                "quantity": this.count,
                "scheduleId": this.$route.query.id
            }
            request.calculate(args).then(res => {
                // console.log(res);
                if (res.success) {
                    this.billCharge = res.object;
                } else {
                    this.tooltip("error", '接口数据请求失败，请稍后再试', 3000, "text", false);
                }
            });
        },
        // 反复购买提示-确认点击
        onConfirm() {

        },
        // 反复购买提示-取消点击
        onCancel() {

        },
        // 购买服务
        buyNow() {
            // this.repeatBuy = true;
            this.isClickBuy = true;
            let args = {
                "studentId": this.userInfo.student.userId,
                "quantity": this.count,
                "scheduleId": this.$route.query.id
            }
            request.payNow(args).then(res => {
                if (res.success) {
                    let params = res.object.param;
                    let outTradeNo = res.object.outTradeNo;
                    setTimeout(() => {
                        this.isClickBuy = false;
                    }, 3000);
                    this.initConfig(params.appId, params.timeStamp, params.nonceStr, () => {
                        wx.ready(() => {
                            wx.chooseWXPay({
                                timestamp: params.timeStamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
                                nonceStr: params.nonceStr, // 支付签名随机串，不长于 32 位
                                package: params.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
                                signType: params.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
                                paySign: params.paySign, // 支付签名
                                success: (payRes) => {
                                    this.$router.push({ path: '/mine', query: { id: outTradeNo, type: "即将开始" } });
                                }
                            });
                        });
                    });
                }
            });
        },
        // 获取当前选中教练订单详情
        queryBillDetail() {
            request.schedule(this.$route.query.id).then(res => {
                // console.warn(res);
                if (res.success) {
                    let cars = res.object;
                    if (!!res.object.schedules[0].tempCoach) {
                        cars.car.coach = res.object.schedules[0].tempCoach;
                    }
                    this.billData = cars;
                } else {
                    this.tooltip("error", '接口数据请求失败，请稍后再试', 3000, "text", false);
                }          
            });
        },
        // 获取收费设置
        queryCharge() {
            let params = [this.userInfo.student.companyId, this.type, this.course];
            request.expenses(params).then(res => {
                // console.log(res);
                if (res.success) {
                    this.chargeSetting = res.object;
                } else {
                    this.tooltip("error", '接口数据请求失败，请稍后再试', 3000, "text", false);                    
                }           
            });
        },
        // 配置微信参数
        initConfig(appId, timestamp, noncestr, callback) {
            let companyId = this.userInfo.student.companyId;
            request.getTicket([companyId, timestamp, noncestr]).then(res => {
                if (res.success) {
                    wx.config({
                        debug: false,
                        appId: appId,
                        timestamp: timestamp,
                        nonceStr: noncestr,
                        signature: res.object.sign,
                        jsApiList: ["chooseWXPay"] // 必填，需要使用的JS接口列表，所有JS接口列表见官方文档附录2
                    });
                    callback();
                }
            });
        }
    },
    created() {
        this.height = '-160';
        this.queryBillDetail();
        this.queryCharge();
    }
}



