import {
    Flexbox,
    FlexboxItem,
    Group,
    GroupTitle,
    Cell,
    Calendar,
    XButton,
    InlineXNumber,
    XDialog,
    Tabbar,
    Scroller,
    TabbarItem
} from "vux"
import {
    global
} from "../assets/javascript/global.js"
import {
    request
} from "../assets/api/api.js"

export default {
    components: {
        Flexbox,
        Group,
        GroupTitle,
        Cell,
        XButton,
        Scroller,
        Calendar,
        InlineXNumber,
        FlexboxItem,
        XDialog,
        Tabbar,
        TabbarItem
    },
    data() {
        return {
            lists: [],
            selectedTab: 2,
            userInfo: JSON.parse(sessionStorage.getItem('userInfo')),
            date: '',
            page: 1,
            totalPage: 1,
            deadlineMsg: '',
            pageSize: global.pageSize,
            pullupConfig: {
                content: '',
                pullUpHeight: 60,
                height: 40,
                autoRefresh: false,
                downContent: '松开进行加载',
                upContent: '上拉加载更多',
                loadingContent: '加载中...'
            },
            status: {
                pullupStatus: 'default'
            },
            isNoData:false
        }
    },
    computed: {
        isAll: function() {
            return this.page > this.totalPage
        }
    },
    methods: {
        onScroll() {
            // console.log('onScroll')
        },
        onPullUpLoad() {
            // 当 page 小于 总页数时 页数递加 并请求新数据
            if (this.page < this.totalPage) {
                this.$refs.scrollerBottom.enablePullup();
                this.page += 1;
                this.getBillHistory();
                this.$nextTick(() => {
                    this.$refs.scrollerBottom.donePullup();
                });
            } else {
                // 当 page 大于或 等于总页数时 禁止上拉 并且不发起新的数据请求
                this.page += 1;
                this.$refs.scrollerBottom.disablePullup();
                this.$nextTick(() => {
                    this.$refs.scrollerBottom.reset();
                    // 底部 无新数据 提示
                    this.deadlineMsg = '已加载全部信息';
                });
            }
        },
        // 行点击
        cellClick(item) {
            var demo = this.format(item, 'font');
            this.$router.push({
                path: '/hasTrained',
                query: {
                    id: item.id,
                    type: demo
                }
            });
        },
        // 底部导航栏点击
        tabbarChanged(val) {
            this.selectedTab = val;
            if (val === 0) {
                this.$router.push({ path: "/" });
            }
            else if (val === 1) {
                this.$router.push({ path: "/scan" });
            }
        },
        // 每一行右侧标签显示文字及字体颜色格式设置
        format(temp, type) {
            if (temp.status === 'PAYED') {
                if (!temp.trainingRecord) {
                    return type === 'bg' ? '#04BE02' : '即将开始'
                } else {
                    if (temp.trainingRecord.status === 'doing') {
                        return type === 'bg' ? '#04BE02' : '培训中'
                    } else if (temp.trainingRecord.status === 'finish') {
                        return type === 'bg' ? '#58B7FF' : '已完成'
                    }
                }
            } else if (temp.status === 'UN_PAY') {
                return type === 'bg' ? 'rgba(255,73,73,0.84)' : '未付款'
            } else if (temp.status === 'REFUND') {
                return type === 'bg' ? '#F7BA2A' : '已退款'
            } else if (temp.status === 'ABANDON') {
                return type === 'bg' ? 'rgba(255,73,73,0.84)' : '已作废'
            }
        },
        // 获取历史培训记录
        getBillHistory() {
            let args = [this.userInfo.student.companyId, this.userInfo.student.userId, this.page, this.pageSize, 'PAYED,REFUND,ABANDON'];
            request.queryOrder(args).then(res => {
                if (res.success) {
                    this.totalPage = res.object.totalPage;
                    if (res.object.list.length) {
                        res.object.list.forEach(item => {
                            this.lists.push({
                                carType: item.carType,
                                coach: item.coach,
                                companyId: item.companyId,
                                course: item.course,
                                date: item.date,
                                fee: item.fee,
                                gmtCreate: item.gmtCreate,
                                gmtPay: item.gmtPay,
                                gmtRefund: item.gmtRefund,
                                id: item.id,
                                license: item.license,
                                operator: item.operator,
                                payTool: item.payTool,
                                quantity: item.quantity,
                                scheduleId: item.scheduleId,
                                sequence: item.sequence,
                                status: item.status,
                                student: item.student,
                                tradeNo: item.tradeNo,
                                trainingRecord: item.trainingRecord,
                                unit: item.unit,
                                verifyCode: item.verifyCode,
                                way: item.way
                            });
                        });
                    }else{
                        this.isNoData = true;
                    }
                } else {
                    this.tooltip("error", '接口数据请求失败，请稍后再试', 5000, "text", false);
                }
            });
        }
    },
    created() {
        this.getBillHistory();
    }
}



