import {
    Flexbox,
    FlexboxItem,
    Group,
    Box,
    Cell,
    XButton,
    Popup,
    Confirm
} from "vux"
import {
    global
} from "../assets/javascript/global.js"
import {
    request
} from "../assets/api/api.js"

export default {
    components: {
        Flexbox,
        Group,
        Cell,
        Box,
        XButton,
        FlexboxItem,
        Popup,
        Confirm
    },
    data() {
        return {
            trainedCancel: false,
            trainedComplete: false,
            afterTrainedCancel: false,
            trainedUseless: false,
            onTraining: false,
            beforeTrain: false,
            shallCancel: false,
            dataById: {
                "id": '',
                "companyId": '',
                "scheduleId": '',
                "license": "",
                "course": "",
                "date": '',
                "way": "",
                "unit": '',
                "quantity": '',
                'gmtRefund':'',
                "fee": '',
                "operator": "",
                "coach": {
                    "userId": '',
                    "userName": "",
                    "telephone": "",
                    "companyId": "",
                    "available": '',
                    "gmtCreate": '',
                    "gender": "",
                    "identityCard": "",
                    "star": '',
                    "photo": ""
                },
                "student": {
                    "userId": '',
                    "userName": "",
                    "telephone": "",
                    "companyId": "",
                    "available": '',
                    "gmtCreate": '',
                    "schoolName": "",
                    "ownSchool": '',
                    "photo": "",
                    "openId": ''
                },
                "status": "",
                "payTool": "",
                "sequence": '',
                "verifyCode": "",
                "trainingRecord": {
                    "id": '',
                    "orderId": '',
                    "status": "",
                    "gmtBegin": '',
                    "gmtEnd": ''
                },
                "gmtCreate": '',
                "gmtPay": ''
            }
        }
    },
    computed: {
        showPop: function() {
            return this.trainedCancel || this.afterTrainedCancel
        }
    },
    methods: {
        onConfirm() {
            //    console.log(msg);
            request.refund(this.$route.query.id).then(res => {
                // console.log(res);
                if (res.success) {
                    this.tooltip("success", '退款成功', 5000, "text", false);
                } else {
                    this.tooltip("text", '接口数据请求失败，请稍后再试', 5000, "text", false);
                }
                setTimeout(() => {
                    this.$router.go(-1);
                }, 5000);
            });
        },
        // 通过订单id获取信息
        queryDataById() {
            // let args = [];
            request.order(this.$route.query.id).then(res => {
                // console.log(res);
                if (res.success) {
                    this.dataById = res.object;
                } else {
                    this.tooltip("error", '接口数据请求失败，请稍后再试', 5000, "text", false);                    
                }             
            });
        },
        // 根据 url 参数判断当前是什么状态
        judgeType(param) {
            if (param === '即将开始') {
                this.beforeTrain = true;
                this.onTraining = false;
                this.trainedComplete = false;
                this.afterTrainedCancel = false;
                this.trainedUseless = false;
            } else if (param === '培训中') {
                this.beforeTrain = false;
                this.onTraining = true;
                this.trainedComplete = false;
                this.afterTrainedCancel = false;
                this.trainedUseless = false;
            } else if (param === '已完成') {
                this.trainedComplete = true;
                this.beforeTrain = false;
                this.onTraining = false;
                this.afterTrainedCancel = false;
                this.trainedUseless = false;
            } else if (param === '未付款') {
                return
            } else if (param === '已退款') {
                this.beforeTrain = false;
                this.trainedUseless = false;
                this.afterTrainedCancel = true;
                this.onTraining = false;
                this.trainedComplete = false;
            } else if (param === '已作废') {
                this.trainedUseless = true;
                this.trainedComplete = false;
                this.beforeTrain = false;
                this.onTraining = false;
                this.afterTrainedCancel = false;
            }
        }
    },
    created() {
        this.judgeType(this.$route.query.type);
        this.queryDataById();
    }
}



