import {
    Msg,
    Box,
    Flexbox,
    FlexboxItem,
    XButton
} from "vux"
export default {
    components: {
        Msg,
        Box,
        Flexbox,
        FlexboxItem,
        XButton
    },
    data() {
        return {
            icon: ""
        }
    },
    methods: {
        changeIcon() {
            if (!this.icon || this.icon === "success") {
                this.icon = "warn";
            } else if (this.icon === "warn") {
                this.icon = "info";
            } else if (this.icon === "info") {
                this.icon = "waiting";
            } else if (this.icon === "waiting") {
                this.icon = "success";
            } else {
                this.icon = "success";
            }
        }
    },
    created() {
        // this.changeIcon();
    }
}



