import {
    XButton,
    Tab,
    Scroller,
    TabItem,
    Flexbox,
    Group,
    Radio,
    FlexboxItem
} from "vux"
import {
    global
} from "../assets/javascript/global.js"
import {
    request
} from "../assets/api/api.js"

export default {
    components: {
        XButton,
        Tab,
        Group,
        Scroller,
        Radio,
        Flexbox,
        FlexboxItem,
        TabItem
    },
    data() {
        return {
            days: [],
            isChooses: 0,
            isNoData: false,
            coaches: [],
            isDown: false,
            sortList: ["默认排序", "星级优先", "空闲优先", "女士优先"],
            carType: this.$route.query.carType,
            course: this.$route.query.course,
            gmtBegin: new Date().Format('yyyy-MM-dd'),
            gmtEnd: new Date().Format('yyyy-MM-dd'),
            sorts: '默认排序',
            userInfo: JSON.parse(sessionStorage.getItem('userInfo')),
            pullupConfig: {
                content: '',
                pullUpHeight: 60,
                height: 40,
                autoRefresh: false,
                downContent: '松开进行加载',
                upContent: '上拉加载更多',
                loadingContent: '加载中...'
            },
            status: {
                pullupStatus: 'default'
            },
            page:1,
            totalPage:1,
            deadlineMsg: ''
        }
    },
    computed: {
        height: function() {
            // let app = navigator.userAgent;
            // if(app.indexOf('iPhone')>-1){
            //     return 570
            // }else if(app.indexOf('Android')>-1){
            //     return 450
            // }
            let height_ = document.documentElement.clientHeight;
            return (height_ - 120);
        },
        isAll: function() {
            return this.page > this.totalPage
        }
    },
    methods: {
        // 上拉刷新
        onPullUpLoad(){
            // 当 page 小于 总页数时 页数递加 并请求新数据
            if (this.page < this.totalPage) {
                this.$refs.scrollerBottom.enablePullup();
                this.page += 1;
                this.queryLists();
                this.$nextTick(() => {
                    this.$refs.scrollerBottom.donePullup();
                });
            } else {
                // 当 page 大于或 等于总页数时 禁止上拉 并且不发起新的数据请求
                this.page += 1;
                this.$refs.scrollerBottom.disablePullup();
                this.$nextTick(() => {
                    this.$refs.scrollerBottom.reset();
                    // 底部 无新数据 提示
                    this.deadlineMsg = '已加载全部信息';
                });
            }
        },
        // 排序选择
        sortChange(val) {
            // console.log(val);
            this.isDown = false;
            switch (val) {
                case '星级排序':

                    break;
                case '空闲优先':

                    break;
                case '女士优先':

                    break;
                default:
                    break;
            }
        },
        // 初始化日期
        getDay() {
            let timestamp = Date.now();
            let tmpDays = [];
            // 递增五天的日期
            for (let i = 0; i < 5; i++) {
                tmpDays.push(new Date(timestamp + 8.64e7 * i));
            }
            // 取每天年月日
            tmpDays.forEach(day => {
                this.days.push(day.Format('yyyy-MM-dd'));
            });
        },
        // 顶部日期切换
        onItemClick(index) {
            this.isChooses = index;
            this.isDown = false;
            this.gmtBegin = this.days[index];
            this.gmtEnd = this.days[index];
            this.queryLists();
        },
        // 行点击之后跳到相应 id 订单详情
        goRouter(item) {
            // console.log(item);
            this.$router.push({
                path: "/choseCoachBill",
                query: {
                    id: item.schedule.id,
                    carType: this.$route.query.carType,
                    course: this.$route.query.course
                }
            });
        },
        // 获取可预约教练列表
        queryLists() {
            let params = [this.userInfo.student.companyId, this.carType, this.course, this.gmtBegin, this.gmtEnd];
            let tmpArr = [];
            this.coaches = [];
            request.query(params).then(res => {
                // console.log(res);
                if (res.success) {
                    this.totalPage = res.object.totalPage;
                    if (res.object.list.length) {
                        res.object.list.forEach((item, index) => {
                            if (!!item.car.coach && item.schedules.length) {
                                tmpArr.push(item);
                            } else {
                                this.isNoData = true;
                            }
                        });
                        if (tmpArr.length) {
                            tmpArr.forEach(item => {
                                let car = item.car;
                                if (!!item.schedules[0].tempCoach) {
                                    car.coach = item.schedules[0].tempCoach;
                                }
                                this.coaches.push({
                                    car: car,
                                    id: item.id,
                                    schedule: item.schedules[0]
                                });
                            });
                        }
                    } else {
                        this.isNoData = true;

                    }
                } else {
                    this.tooltip("error", '数据请求失败，请稍后重试', 5000, "text", false);
                }
            });
        }
    },
    created() {
        this.getDay();
        this.queryLists();
        // console.log('documentElement.clientHeight: ' + document.documentElement.clientHeight);
    }
}



